import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { CardSneakers } from '../../cardSneakers/CardSneakers';
import axios from 'axios';

import './Orders.scss';

export const Orders = () => {
    const [orders, setOrders] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        async function fetchData() {
            setIsLoading(true);
            try {
                const { data } = await axios.get('https://6253fc5019bc53e234769c4f.mockapi.io/Orders');
                setIsLoading(false);
                setOrders(data.map((obj) => obj.items).flat());
            } catch (error) {
                alert('   ...')
            } finally {
                setIsLoading(false);
            }
        }
        fetchData();
    }, []);

    return (
        
        <main>
            {isLoading ? (

                <div className="favorite__header">
                    <Link to='/'><img src="images/arrowPrevBlack.svg" alt="" /></Link>
                    <div className="title"> ...</div>
                </div>

            ) : orders.length > 0 ? (
                <>
                    <div className="favorite__header">
                        <Link to='/'><img src="images/arrowPrevBlack.svg" alt="" /></Link>
                        <div className="title"></div>
                    </div>

                    <div className="sneakersFavorite">
                        {(isLoading ? [...Array(5)] : orders).map((item, index) => (
                            <CardSneakers
                                key={index}
                                {...item}
                                loading={isLoading}
                            />
                        ))}
                    </div>
                </>
            ) : (
                <div className="nullFavorite">
                    <img src="images/nullOrder.jpg" alt=" " />
                    <h3>   ...</h3>
                    <p>     </p>
                    <Link to="/"><button className='cartBtnPrev'><img src="images/ArrowPrev.svg" alt="" /> </button></Link>
                </div>
            )}
        </main>
    )
}
